<template>
  <v-card outlined :key="renderCount">
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> mdi-arrow-left </v-icon>
      </v-btn>
      Producers
    </v-card-title>

    <v-card-text>
      <breadcrumbs :items="breadcrumbItems"></breadcrumbs>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <producers-grid v-if="params.uuid" :params="params" :getDataCb="getDataCb"> </producers-grid>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>
<script>
import ProducersGrid from './producersGrid.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import IconButton from '../../Common/IconButton.vue';
import Breadcrumbs from '../../Common/Breadcrumbs.vue';
import _get from 'lodash/get';
import ROUTES from '../../../constants/routes';

export default {
  name: 'producers',
  data() {
    return {
      params: {
        uuid: null,
        with_producers: true,
      },
      gridData: {},
      renderCount: 0,
      breadcrumbItems: [],
    };
  },
  components: {
    IconButton,
    ProducersGrid,
    TooltipIconButton,
    Breadcrumbs,
  },
  mounted() {
    this.initialize();
  },
  watch: {
    '$route.params.streamId': function (val, oldVal) {
      if (val != oldVal) {
        this.renderCount++;
        this.initialize();
      }
    },
  },
  methods: {
    initialize() {
      this.params = { ...this.params, uuid: this.$route.params.streamId };
      this.updateBreadCrumbs();
    },
    onBack() {
      this.$router.go(-1);
    },
    getDataCb(data) {
      this.gridData = data;
    },
    updateBreadCrumbs() {
      this.breadcrumbItems = [];
      this.breadcrumbItems.push({
        text: 'Flow Network',
        align: 'center',
        disabled: true,
        link: ROUTES.rfFlowNetwork,
      });
      this.breadcrumbItems.push({ text: this.$route.params.flowNetworkId, align: 'center', disabled: false });
      this.breadcrumbItems.push({
        text: 'Streams',
        align: 'center',
        disabled: true,
        link: ROUTES.rfFlowNetworkStreams.replace(':flowNetworkId', this.$route.params.flowNetworkId),
      });
      this.breadcrumbItems.push({ text: this.$route.params.streamId, align: 'center', disabled: false });
      this.breadcrumbItems.push({ text: 'Producers', align: 'center', disabled: true });
    },
  },
};
</script>
