<template>
  <v-card>
    <v-card-title>
      <span class="headline">{{ title }}</span>
    </v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-text-field required v-model="formValues.name" label="Name" :rules="[rules.required]"></v-text-field>
          </v-col>
          <v-col cols="6">
            <v-switch v-model="formValues.enable" inset class="pr-4" label="Enable" color="primary"></v-switch>
          </v-col>
          <v-col cols="6">
            <v-switch
              v-model="formValues.enable_history"
              inset
              class="pr-4"
              label="Save History"
              color="primary"
            ></v-switch>
          </v-col>
          <v-col cols="12">
            <v-select
              label="Thing Class"
              item-value="name"
              @change="onThingTypeChange"
              :items="thingTypes"
              item-text="displayName"
              v-model="formValues.producer_thing_class"
            ></v-select>
          </v-col>

          <v-col cols="12" v-if="formValues.producer_thing_class === 'network'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Network"
              item-value="uuid"
              item-text="name"
              :items="networks"
              deletable-chips
              :rules="[rules.required]"
              v-model="formValues.producer_thing_uuid"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" v-if="formValues.producer_thing_class === 'device'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Device"
              item-value="uuid"
              item-text="name"
              :items="devices"
              :rules="[rules.required]"
              deletable-chips
              v-model="formValues.producer_thing_uuid"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" v-if="formValues.producer_thing_class === 'point'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Point"
              item-value="uuid"
              item-text="displayName"
              :items="allPoints"
              :loading="isFetchingAllDevices"
              :rules="[rules.required]"
              deletable-chips
              v-model="formValues.producer_thing_uuid"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" v-if="formValues.producer_thing_class === 'schedule'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Scheulde"
              item-value="uuid"
              item-text="name"
              :items="schedules"
              :loading="isFetchingSchedules"
              :rules="[rules.required]"
              deletable-chips
              v-model="formValues.producer_thing_uuid"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12" v-if="formValues.producer_thing_class === 'point_filter'">
            <v-autocomplete
              chips
              clearable
              small-chips
              label="Network"
              item-value="uuid"
              item-text="name"
              :items="networks"
              :rules="[rules.required]"
              @change="fetchChildDevicesByNetworkId"
              deletable-chips
              v-model="selectedNetwork"
            ></v-autocomplete>

            <v-autocomplete
              :disabled="!selectedNetwork"
              chips
              :loading="isFetchingNetworkDevices"
              clearable
              small-chips
              label="Device"
              item-value="uuid"
              item-text="name"
              @change="fetchChildPointsByDeviceId"
              :items="devices"
              :rules="[rules.required]"
              deletable-chips
              v-model="selectedDevice"
            ></v-autocomplete>

            <v-autocomplete
              chips
              :disabled="!selectedDevice"
              clearable
              :loading="isFetchingDevicePoints"
              small-chips
              label="Point"
              item-value="uuid"
              item-text="name"
              :items="points"
              :rules="[rules.required]"
              deletable-chips
              v-model="formValues.producer_thing_uuid"
            ></v-autocomplete>
          </v-col>

          <v-col cols="12">
            <v-select
              label="Thing Action"
              v-model="formValues.producer_application"
              :items="thingActions"
              item-text="displayName"
              item-value="name"
            ></v-select>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-spacer></v-spacer>
    <v-card-actions class="align-end justify-end">
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :loading="isSaving" text @click="onSubmit(formValues)"> {{ saveTitle }} </v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import _cloneDeep from 'lodash/cloneDeep';
import { THING_TYPES, THINGS_TYPES_MAP, THING_ACTIONS } from './producers.constant';
import { rfNetworkService } from '../../../services/rfNetworkService';
import { rfDeviceService } from '../../../services/rfDeviceService';

export const initialFormValues = () => ({
  name: 'producer',
  enable: false,
  producer_thing_class: 'point',
  enable_history: false,
  producer_application: 'mapping',
  stream_uuid: null,
  producer_thing_uuid: null,
});
export default {
  name: 'producers-form',
  props: ['isEdit', 'onCancel', 'onSave', 'item', 'isSaving', 'stream', 'schedules', 'allPoints'],
  data: () => ({
    isFetchingAllDevices: false,
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
    devices: [],
    points: [],
    isFetchingNetworkDevices: false,
    isFetchingDevicePoints: false,
    isFetchingSchedules: false,
    selectedNetwork: null,
    selectedDevice: null,
    thingTypes: THING_TYPES,
    thingActions: THING_ACTIONS,
    valid: false,
    formValues: {
      ...initialFormValues(),
    },
  }),
  computed: {
    lowerCaseTitle() {
      return 'producers';
    },
    titleCase() {
      return 'Producers';
    },
    title() {
      return this.isEdit ? `Edit ${this.titleCase}` : `Create ${this.titleCase}`;
    },
    saveTitle() {
      return this.isEdit ? 'Save' : 'Create';
    },
    networks() {
      return this.$store.state.rfNetworks;
    },
  },
  mounted() {
    if (this.isEdit && this.item) {
      this.formValues = {
        ...this.formValues,
        ...this.item,
      };
    }

    if (!this.isEdit && this.stream) {
      this.formValues = {
        ...this.formValues,
        stream_uuid: this.stream.uuid,
      };
    }
  },
  methods: {
    cleanUp() {
      this.formValues = {};
      if (typeof this.onCancel === 'function') this.onCancel();
    },
    onThingTypeChange() {
      this.formValues.producer_thing_uuid = null;
    },
    fetchChildDevicesByNetworkId(networkId) {
      if (!networkId) {
        this.selectedDevice = null;
        this.formValues.producer_thing_uuid = null;
        return;
      }
      this.isFetchingNetworkDevices = true;
      rfNetworkService
        .fetchById(networkId, { with_devices: true })
        .then(data => {
          this.devices = data.devices;
        })
        .finally(() => {
          this.isFetchingNetworkDevices = false;
        });
    },
    fetchChildPointsByDeviceId(deviceId) {
      if (!deviceId) {
        this.formValues.producer_thing_uuid = null;
        return;
      }
      this.isFetchingDevicePoints = true;
      rfDeviceService
        .fetchById(deviceId, { with_points: true })
        .then(data => {
          this.points = data.points;
        })
        .finally(() => {
          this.isFetchingDevicePoints = false;
        });
    },
    transformFormValues(formValues) {
      let values = _cloneDeep(formValues);
      values.producer_thing_class = THINGS_TYPES_MAP[values.producer_thing_class].value;
      return values;
    },
    onSubmit() {
      if (!this.$refs.form.validate()) {
        return;
      }
      if (typeof this.onSave !== 'function') {
        throw new Error(`${this.lowerCaseTitle} form onSave function not passed`);
      }
      return this.onSave(this.transformFormValues(this.formValues))
        .then(() => {
          this.$toasted.show(`Successfully ${this.isEdit ? 'updated' : 'created'} ${this.lowerCaseTitle}.`);
          this.cleanUp(true);
        })
        .catch(() => {
          this.$toasted.show(`Failed to ${this.isEdit ? 'update' : 'create'} ${this.lowerCaseTitle}.`, {
            type: 'error',
          });
        });
    },
  },
};
</script>
