<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :params="params"
      :service="service"
      :headers="gridHeaders"
      :getDataCb="getDataCb"
      :toolbarTitle="toolbarTitle"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewWriterClones, item)"></text-button>
      </template>
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <producers-form
          v-if="isOpen"
          :stream="stream"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
          :schedules="schedules"
          :allPoints="allPoints"
        ></producers-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <producers-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
          :schedules="schedules"
          :allPoints="allPoints"
        ></producers-form>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import ProducersForm from './producersForm.vue';
import IconButton from '../../Common/IconButton.vue';
import TextButton from '../../Common/TextButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import { rfNetworkService } from '../../../services/rfNetworkService';
import { rfScheduleService } from '../../../services/rfScheduleService';
import { rfProducersService } from '../../../services/rfProducersService';
import * as rfNetworkUiService from '../../../services/rfNetworkUiService';

const dialogNames = {
  viewWriterClones: 'viewWriterClones',
};

const redirectMap = {
  [dialogNames.viewWriterClones]: (flowNetworkId, streamId, producerId) =>
    ROUTES.rfFlowNetworkWriterClone
      .replace(':streamId', streamId)
      .replace(':flowNetworkId', flowNetworkId)
      .replace(':producerId', producerId),
};
export default {
  components: { GenericGrid, ProducersForm, IconButton, TextButton },
  name: 'producersGrid',
  props: ['params', 'getDataCb'],
  data() {
    return {
      schedules: [],
      allPoints: [],
      service: rfProducersService,
      dialogNames: {
        ...dialogNames,
      },
      toolbarTitle: 'Producers',
      gridName: gridNames.RF_PRODUCERS,
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  mounted() {
    this.fetchSchedules();
    this.fetchAllPoint();
  },
  computed: {
    flowNetworkId() {
      return this.$route.params.flowNetworkId;
    },
    streamId() {
      return this.$route.params.streamId;
    },
    stream() {
      return {
        uuid: this.params.uuid,
      };
    },
  },
  methods: {
    fetchSchedules() {
      rfScheduleService
        .fetch()
        .then(schedules => {
          this.schedules = schedules;
          return schedules;
        })
    },
    fetchAllPoint() {
      rfNetworkService
        .fetch({ with_points: true })
        .then(networks => {
          this.allPoints = rfNetworkUiService.getFlatPoints(networks);
          return networks;
        })
    },
    redirectTo(viewName, item) {
      const producerId = item.uuid;
      if (!producerId) {
        return;
      }

      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(this.flowNetworkId, this.streamId, producerId) });
      }
    },
  },
};
</script>
