import BaseService from './baseService';
import endpoints from '../constants/endpoints';
import http from '../axios';
import _omit from 'lodash/omit';
import qs from 'query-string';

class RFProducersService extends BaseService {
  constructor() {
    super(endpoints.rfProducers);
  }

  fetchAll() {
    let endpoint = endpoints.rfProducers();
    return http.get(endpoint);
  }

  fetch = (query = {}) => {
    const streamId = query.uuid || query.id;
    if (!streamId) {
      throw new Error('Stream Id is required for fetching producers of streams');
    }

    let endpoint = endpoints.rfStreams(streamId);
    query = _omit(query, ['uuid', 'id']);

    if (query && Object.keys(query).length > 0) {
      endpoint = endpoint + '?' + qs.stringify(query);
    }

    return http.get(endpoint).then(data => {
      return { dataItems: data.producers, extraData: _omit(data, ['producers']) };
    });
  };
}

export const rfProducersService = new RFProducersService();
